import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AccountSettingsService } from 'src/services/account-settings.service';
import { IUser } from 'src/interfaces/user';

@Component({
  selector: 'app-account-settings',
  templateUrl: './account-settings.component.html',
  styleUrls: ['./account-settings.component.scss']
})
export class AccountSettingsComponent implements OnInit {
  constructor(private accountSettingsService: AccountSettingsService) {}
  users: IUser[] = [];

  private getUsers(param = {}) {
    this.accountSettingsService.getUsers(param).subscribe(
      (res) => {
        this.users = res;
        console.log(res);
        console.log(param);
      },
      (err) => {
        console.log(err);
      }
    );
  }
  form: FormGroup;
  ngOnInit() {
    this.form = new FormGroup({
      email: new FormControl('', [Validators.required, Validators.email]),
      firstName: new FormControl('', Validators.required),
      lastName: new FormControl('', Validators.required),
      zip: new FormControl('', [
        Validators.required,
        Validators.minLength(8),
        Validators.maxLength(8)
      ]),
      city: new FormControl('', Validators.required),
      state: new FormControl('', Validators.required)
    });
    this.getUsers();
  }
  onSubmit() {
    console.log('Submitted', this.form);
  }

  isFieldValid = (filedName) => {
    return this.form.get(filedName).invalid && this.form.get(filedName).touched;
  };
}
